@use './variables' as vars;
@use './mixins' as mixins;

body {
  height: 100%;
  background-attachment: fixed;
  background-color: vars.$c-peach;
  box-sizing: border-box;
  background-image: url("../img/peek-sm.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-size: vars.$baseFontSize;
  color: vars.$c-text;
}

p {
  line-height: 1.9;
  margin: 0 0 1rem 0;
}

/* FORM FIELD STYLES */
fieldset,
form,
label,
legend {
  width: 100%;
  margin: 0;
  padding: 0 0 0 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

sup {
  color: vars.$c-warning;
  font-size: 2rem;
  line-height: 0;
  position: relative;
  top: 8px;
}

label {
  font-size: vars.$baseFontSize;
  order: 1;
  min-width: 100%;

  @include mixins.bp(small) {
    @include mixins.font-size(18);
  }
}

select,
input[type="number"],
input[type="text"] {
  box-sizing: border-box;
  display: block;
  margin: 5px 0 3px 0;
  width: 100%;
  min-height: 35px;
  height: 35px;
  padding: 8px;
  color: vars.$c-grey-med;
  order: 2;
  background-color: transparent;
  border: 1px solid vars.$c-grey-med;
  outline: none;
  border-radius: 4px;
  flex: 1 0 0;
  @include mixins.font-size(14);

  @include mixins.bp-arb(vars.$bp-xsmall) {
    min-width: 75%;
  }
  @include mixins.bp(small) {
    min-width: 50%;
  }
}

#firstName {
  width: 100%;
  margin: 10px auto;

  @include mixins.bp(small) {
    width: 50%;
  }
}

option {
  font-size: vars.$baseFontSize;
  text-transform: none;
  @include mixins.bp(small) {
    @include mixins.font-size(27);
  }
}

a {
  color: vars.$c-text;
  &:hover {
    color: vars.$c-white;
    text-decoration: none;
    border-radius: 0;
    box-shadow: none;
    outline: 1px solid vars.$c-brown;
  }
}

.container {
  display: flex;             
  flex-flow: column;
  margin: 0 auto;
  padding: 30px;
  max-width: 75%;
  min-width: 255px;
  font-family: vars.$f-text;
  border: none;
  color: vars.$c-text;
  position: relative;

  @include mixins.bp(small) { max-width: 71%; }
  @include mixins.bp(medium) { max-width: 675px; }
  @include mixins.bp(large) { max-width: 820px; }

  /* semi-transparent white box */
  &:after {
    content: "";
    background-color: vars.$c-peach;
    position: absolute;
    opacity: 0.95;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
  }

  h1 { 
    order: 0; 
    font-family: vars.$f-title;
    text-align: center;
    margin: 0 0 1rem 0;
    @include mixins.font-size(36);

    @include mixins.bp(small) { @include mixins.font-size(48); }
    @include mixins.bp(medium) { @include mixins.font-size(60); }
  	@include mixins.bp(large) { @include mixins.font-size(72); }
  }

  .enter-name {
    display: block;
    flex: 0 1 auto;
    text-align: center;
    flex-wrap: nowrap;
    padding: 0 0 0 15px;
  }

  .q1, .q2, .q3 {
    display: flex; 
    flex-flow: column nowrap;
  }

  .question {
    display: flex;
    flex-flow: column wrap;
    position: relative;
    margin: 0;


    @include mixins.bp(small) { flex-flow: column wrap; }

    @include mixins.bp(medium) { 
      flex-flow: row wrap; 
      margin: 0 0 1rem 0; 
    }
  }

  .error-name,
  .error {
    // @include mixins.font-size(14);
    height: 20px;
    margin: 0;
    font-style: italic;
    color: vars.$c-warning;
    max-width: 100%;
    order: 3;
    flex: 2 0 auto;

    @include mixins.bp(medium) { padding:  0 0 15px 0; }
  }
}

/* BUTTONS */
.btn {
  display: block;
  margin: 0;
  padding: 0.6rem 1rem;
  border-style: none;
  border: 1px solid transparent;
  border-radius: 4px;
  color: vars.$c-white;
  font-size: vars.$baseFontSize;
  order: 3;

  + p {
    display: inline-block;
    @include mixins.font-size(14);
    height: 1rem;
    margin: 0;
    font-style: italic;
    color: vars.$c-warning;
    order: 4;
  }
}

.btn-orange {
  background-color: vars.$c-brown;
  
  &:hover {
    box-shadow: none;
  border: 1px solid vars.$c-brown;
    background-color: vars.$c-white;
  color: vars.$c-brown;
    text-decoration: none;
  }
}

#btn-name, 
.btn-reset { margin: 0 auto; }

a:active, select:focus, button[type="submit"]:focus, button[type="submit"]:active, input[type="number"]:focus, input[type="text"]:focus {
  border-color: vars.$c-brown;
  box-shadow: none;
  outline: auto;
}

.listings {
  display: block;
  width: 100%;
  margin: 1rem auto;
  order: 6;
  @include mixins.font-size(18);

  @include mixins.bp(xsmall) {
    width: 90%;
    font-size: vars.$baseFontSize;
  }
  @include mixins.bp(small) {
    width: 75%;
    margin: 0 auto 2rem auto;
  }
  @include mixins.bp(large) { width: 60%; }

  .img-print-only { display: none; }

  .js-list1, .js-list2, .js-list3 {
    margin: 0;
    @include mixins.bp(small) { margin: 0 0 2rem 0; }
  }
}

p {
  line-height: vars.$baseLineHeight; 
  margin: 0 0 1rem 0; 
}

span { text-transform: lowercase; }
span.landmark { text-transform: none; }
span.firstName, span.water, span.bestie, span.favCartoon, span.favCountry, span.favAnimal, span.verb1, span.artist, span.prez, span.favCity, span.error, span.error-name { text-transform: capitalize; }

.share {
  display: flex;
  flex-flow: column nowrap;
  order: 9;
  width: 100%;
  margin: 0.3rem auto 0 auto;

  p {
    flex: 0 1 auto;
    width: 100%;
    margin: 1rem 0 0 0;
    @include mixins.font-size(14);
    text-align: center;
  }

  .btn-copy { order: 1; }

  .btn-ghost {
    color: vars.$c-text;
    background-color: transparent;
    border: 1px solid transparent;
    @include mixins.font-size(14);
    margin: 0 auto;
    padding: 15px;
    text-decoration: none;
    &:hover { border: 1px solid vars.$c-text; outline: none; border-radius: 4px; }
    svg { height: 15px; margin: 0 0 0 3px; }
    @include mixins.bp(medium) { display: block; }
  }

  .icons {
    order: 2;
    display: inline-flex;
    justify-content: center;
    margin: 0.3rem auto 0 auto;
    height: 30px;

    .email { a { svg { margin-top: 5px; height: 30px; } } }
    .share-icon { margin: 0 10px; height: 100%; a { &:hover { border: none; outline: none; } svg { height: 25px; } } }
    .fb-share-icon { span { visibility: hidden; .svg { fill: vars.$c-white; } } }
  }

  .btn-about { order: 3; }

  .eliza {
    order: 4;
    margin: 1rem 0 0 0;
    text-align: center;
    color: vars.$c-text;
    a {
      color: vars.$c-text;
      border: 1px solid transparent;
      @include mixins.font-size(14);
      margin: 0 auto;
      padding: 15px;
      text-decoration: none;
      &:hover { border: 1px solid vars.$c-text; outline: none; border-radius: 4px; }
      &:active { color: vars.$c-brown; }
      @include mixins.bp(xsmall) { &:hover { border: none; } }
      svg { height: 12px; margin: 0 0 0 3px; }
    }
  }
}

/* MODAL STYLES */
.modal-on { overflow: hidden; }

.modal-container {
  order: 3;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: vars.$c-peach;

  .modal {
    background-color: vars.$c-peach;
    width: 80%;
    margin: 0 auto;
    border-radius: 20px;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    text-align: left;

    @include mixins.bp(small) { width: 40%; }
    @include mixins.bp(medium) { top: 30%; }
  }

  .modal-header { width: 90%; margin: 0 auto; position: relative; }

  .close { position: absolute; text-transform: uppercase; @include mixins.font-size(12); right: 0; top: 0; }

  .modal-content {
    width: 90%;
    margin: 0 auto;
    text-align: left;
    p.explain { text-align: left; a { color: vars.$c-text; } }
    p.copyright { @include mixins.font-size(12); }
  }
}
