body {
  height: 100%;
  background-attachment: fixed;
  background-color: rgb(255, 243, 232);
  box-sizing: border-box;
  background-image: url("../img/peek-sm.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-size: 16px;
  color: rgb(63, 63, 61);
}

p {
  line-height: 1.9;
  margin: 0 0 1rem 0;
}

/* FORM FIELD STYLES */
fieldset,
form,
label,
legend {
  width: 100%;
  margin: 0;
  padding: 0 0 0 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

sup {
  color: #D9003A;
  font-size: 2rem;
  line-height: 0;
  position: relative;
  top: 8px;
}

label {
  font-size: 16px;
  order: 1;
  min-width: 100%;
}
@media only screen and (min-width: 768px) {
  label {
    font-size: 1.125rem;
    font-size: 18px;
  }
}

select,
input[type=number],
input[type=text] {
  box-sizing: border-box;
  display: block;
  margin: 5px 0 3px 0;
  width: 100%;
  min-height: 35px;
  height: 35px;
  padding: 8px;
  color: #636363;
  order: 2;
  background-color: transparent;
  border: 1px solid #636363;
  outline: none;
  border-radius: 4px;
  flex: 1 0 0;
  font-size: 0.875rem;
  font-size: 14px;
}
@media only screen and (min-width: 340px) {
  select,
  input[type=number],
  input[type=text] {
    min-width: 75%;
  }
}
@media only screen and (min-width: 768px) {
  select,
  input[type=number],
  input[type=text] {
    min-width: 50%;
  }
}

#firstName {
  width: 100%;
  margin: 10px auto;
}
@media only screen and (min-width: 768px) {
  #firstName {
    width: 50%;
  }
}

option {
  font-size: 16px;
  text-transform: none;
}
@media only screen and (min-width: 768px) {
  option {
    font-size: 1.6875rem;
    font-size: 27px;
  }
}

a {
  color: rgb(63, 63, 61);
}
a:hover {
  color: #fff;
  text-decoration: none;
  border-radius: 0;
  box-shadow: none;
  outline: 1px solid #6e4f1e;
}

.container {
  display: flex;
  flex-flow: column;
  margin: 0 auto;
  padding: 30px;
  max-width: 75%;
  min-width: 255px;
  font-family: "Raleway", sans-serif;
  border: none;
  color: rgb(63, 63, 61);
  position: relative;
}
@media only screen and (min-width: 768px) {
  .container {
    max-width: 71%;
  }
}
@media only screen and (min-width: 1024px) {
  .container {
    max-width: 675px;
  }
}
@media only screen and (min-width: 1200px) {
  .container {
    max-width: 820px;
  }
}
.container {
  /* semi-transparent white box */
}
.container:after {
  content: "";
  background-color: rgb(255, 243, 232);
  position: absolute;
  opacity: 0.95;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}
.container h1 {
  order: 0;
  font-family: "Majesti-Banner-Heavy", serif;
  text-align: center;
  margin: 0 0 1rem 0;
  font-size: 2.25rem;
  font-size: 36px;
}
@media only screen and (min-width: 768px) {
  .container h1 {
    font-size: 3rem;
    font-size: 48px;
  }
}
@media only screen and (min-width: 1024px) {
  .container h1 {
    font-size: 3.75rem;
    font-size: 60px;
  }
}
@media only screen and (min-width: 1200px) {
  .container h1 {
    font-size: 4.5rem;
    font-size: 72px;
  }
}
.container .enter-name {
  display: block;
  flex: 0 1 auto;
  text-align: center;
  flex-wrap: nowrap;
  padding: 0 0 0 15px;
}
.container .q1, .container .q2, .container .q3 {
  display: flex;
  flex-flow: column nowrap;
}
.container .question {
  display: flex;
  flex-flow: column wrap;
  position: relative;
  margin: 0;
}
@media only screen and (min-width: 768px) {
  .container .question {
    flex-flow: column wrap;
  }
}
@media only screen and (min-width: 1024px) {
  .container .question {
    flex-flow: row wrap;
    margin: 0 0 1rem 0;
  }
}
.container .error-name,
.container .error {
  height: 20px;
  margin: 0;
  font-style: italic;
  color: #D9003A;
  max-width: 100%;
  order: 3;
  flex: 2 0 auto;
}
@media only screen and (min-width: 1024px) {
  .container .error-name,
  .container .error {
    padding: 0 0 15px 0;
  }
}

/* BUTTONS */
.btn {
  display: block;
  margin: 0;
  padding: 0.6rem 1rem;
  border-style: none;
  border: 1px solid transparent;
  border-radius: 4px;
  color: #fff;
  font-size: 16px;
  order: 3;
}
.btn + p {
  display: inline-block;
  font-size: 0.875rem;
  font-size: 14px;
  height: 1rem;
  margin: 0;
  font-style: italic;
  color: #D9003A;
  order: 4;
}

.btn-orange {
  background-color: #6e4f1e;
}
.btn-orange:hover {
  box-shadow: none;
  border: 1px solid #6e4f1e;
  background-color: #fff;
  color: #6e4f1e;
  text-decoration: none;
}

#btn-name,
.btn-reset {
  margin: 0 auto;
}

a:active, select:focus, button[type=submit]:focus, button[type=submit]:active, input[type=number]:focus, input[type=text]:focus {
  border-color: #6e4f1e;
  box-shadow: none;
  outline: auto;
}

.listings {
  display: block;
  width: 100%;
  margin: 1rem auto;
  order: 6;
  font-size: 1.125rem;
  font-size: 18px;
}
@media only screen and (min-width: 340px) {
  .listings {
    width: 90%;
    font-size: 16px;
  }
}
@media only screen and (min-width: 768px) {
  .listings {
    width: 75%;
    margin: 0 auto 2rem auto;
  }
}
@media only screen and (min-width: 1200px) {
  .listings {
    width: 60%;
  }
}
.listings .img-print-only {
  display: none;
}
.listings .js-list1, .listings .js-list2, .listings .js-list3 {
  margin: 0;
}
@media only screen and (min-width: 768px) {
  .listings .js-list1, .listings .js-list2, .listings .js-list3 {
    margin: 0 0 2rem 0;
  }
}

p {
  line-height: 1.8;
  margin: 0 0 1rem 0;
}

span {
  text-transform: lowercase;
}

span.landmark {
  text-transform: none;
}

span.firstName, span.water, span.bestie, span.favCartoon, span.favCountry, span.favAnimal, span.verb1, span.artist, span.prez, span.favCity, span.error, span.error-name {
  text-transform: capitalize;
}

.share {
  display: flex;
  flex-flow: column nowrap;
  order: 9;
  width: 100%;
  margin: 0.3rem auto 0 auto;
}
.share p {
  flex: 0 1 auto;
  width: 100%;
  margin: 1rem 0 0 0;
  font-size: 0.875rem;
  font-size: 14px;
  text-align: center;
}
.share .btn-copy {
  order: 1;
}
.share .btn-ghost {
  color: rgb(63, 63, 61);
  background-color: transparent;
  border: 1px solid transparent;
  font-size: 0.875rem;
  font-size: 14px;
  margin: 0 auto;
  padding: 15px;
  text-decoration: none;
}
.share .btn-ghost:hover {
  border: 1px solid rgb(63, 63, 61);
  outline: none;
  border-radius: 4px;
}
.share .btn-ghost svg {
  height: 15px;
  margin: 0 0 0 3px;
}
@media only screen and (min-width: 1024px) {
  .share .btn-ghost {
    display: block;
  }
}
.share .icons {
  order: 2;
  display: inline-flex;
  justify-content: center;
  margin: 0.3rem auto 0 auto;
  height: 30px;
}
.share .icons .email a svg {
  margin-top: 5px;
  height: 30px;
}
.share .icons .share-icon {
  margin: 0 10px;
  height: 100%;
}
.share .icons .share-icon a:hover {
  border: none;
  outline: none;
}
.share .icons .share-icon a svg {
  height: 25px;
}
.share .icons .fb-share-icon span {
  visibility: hidden;
}
.share .icons .fb-share-icon span .svg {
  fill: #fff;
}
.share .btn-about {
  order: 3;
}
.share .eliza {
  order: 4;
  margin: 1rem 0 0 0;
  text-align: center;
  color: rgb(63, 63, 61);
}
.share .eliza a {
  color: rgb(63, 63, 61);
  border: 1px solid transparent;
  font-size: 0.875rem;
  font-size: 14px;
  margin: 0 auto;
  padding: 15px;
  text-decoration: none;
}
.share .eliza a:hover {
  border: 1px solid rgb(63, 63, 61);
  outline: none;
  border-radius: 4px;
}
.share .eliza a:active {
  color: #6e4f1e;
}
@media only screen and (min-width: 340px) {
  .share .eliza a:hover {
    border: none;
  }
}
.share .eliza a svg {
  height: 12px;
  margin: 0 0 0 3px;
}

/* MODAL STYLES */
.modal-on {
  overflow: hidden;
}

.modal-container {
  order: 3;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(255, 243, 232);
}
.modal-container .modal {
  background-color: rgb(255, 243, 232);
  width: 80%;
  margin: 0 auto;
  border-radius: 20px;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  text-align: left;
}
@media only screen and (min-width: 768px) {
  .modal-container .modal {
    width: 40%;
  }
}
@media only screen and (min-width: 1024px) {
  .modal-container .modal {
    top: 30%;
  }
}
.modal-container .modal-header {
  width: 90%;
  margin: 0 auto;
  position: relative;
}
.modal-container .close {
  position: absolute;
  text-transform: uppercase;
  font-size: 0.75rem;
  font-size: 12px;
  right: 0;
  top: 0;
}
.modal-container .modal-content {
  width: 90%;
  margin: 0 auto;
  text-align: left;
}
.modal-container .modal-content p.explain {
  text-align: left;
}
.modal-container .modal-content p.explain a {
  color: rgb(63, 63, 61);
}
.modal-container .modal-content p.copyright {
  font-size: 0.75rem;
  font-size: 12px;
}

/********************
** Partials
********************/
@media print {
  @page {
    size: 8.5in 11in portrait;
    margin: 0.5in 0.25in 0.25in 0.25in;
  }
  body {
    background: #fff;
    width: 100% !important;
  }
  body .container {
    max-width: 70% !important;
    border: 1px solid #6e4f1e;
  }
  body .container .message {
    font-size: 50px;
  }
  body .container .listings {
    margin-bottom: 0;
  }
  body .container .listings .img-print-only {
    display: block;
    max-width: 100%;
    margin: auto;
  }
  body .container .share .icons {
    display: none;
  }
  body .container .share button {
    display: none;
  }
  body .container .share .eliza a > svg {
    display: none;
  }
  body .container .share .eliza a[href^=http]:after {
    display: block;
    width: 100%;
    margin: 0 auto;
    content: "Visit www.eliza.liveinsouthjersey.com";
  }
}